import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RoleModel } from 'src/app/shared/models/model';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';

@Component({
  selector: 'app-role',
  templateUrl: './role.component.html',
  styleUrls: ['./role.component.scss']
})
export class RoleComponent implements OnInit {
  RoleForm!: FormGroup;
  roles: any[] = [];
  display: boolean = false;

  constructor(private fb: FormBuilder,private roleService: RoleService, private toastrService: ToastrService) { 
    this.RoleForm = this.fb.group({
      roleId:[''],
      roleName: ['', Validators.required]
    });
  }


  ngOnInit() {
    this.getRoles();
  }

  getRoles() {
    this.roleService.getAll().subscribe((response: any) => {
      this.roles = response;
    });
  }

  showDialog() {
    this.display = true;
    this.RoleForm.reset();
  }
  updateData() {
    let temp = [...this.roles];
    temp = JSON.parse(JSON.stringify(temp));
    this.roles = temp;
  }


  add() {
    const data = this.RoleForm.value;
    if(!data.roleId){
      delete data.roleId;
    }

    this.roleService.create(data).subscribe({
      next: (response: any) => {
      if (!!response.roleId) {
        this.roles.push(data);
        this.updateData();
        this.toastrService.success('Role Create Successfully.');
        this.display = false;
      }
    },
    error: () => {
      this.toastrService.error('Role Create Failed.');
    }
    });
  }

  edit() {
    this.roleService.edit(this.RoleForm.value).subscribe((response: any) => {
        const data = this.RoleForm.value;
        const index = this.roles.findIndex(i => i.roleId === data.roleId);
        this.roles[index] = data;
        this.toastrService.success(response.message ?? 'Role Updated Successfully.');
        this.display = false;
    });
  }


  onSubmit(){
    if (this.RoleForm.invalid) {
      return;
    }

    if (!this.RoleForm.value.roleId) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(role: RoleModel) {
    this.display = true;
    this.RoleForm.patchValue(role);
  }

  deleteBranch(id: number) {
    this.roleService.delete(id).subscribe((response: any) => {
      const index = this.roles.findIndex(i => i.roleId == id);
      this.roles.splice(index,1);
      this.toastrService.success('Role Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}
